.shower.full {
  background-color: #111;
}
.shower.list .slide.active {
  box-shadow: 0 0 0 1px #888, 0 0 0 20px #888, 0 20px 50px rgba(0,0,0,0.3);
}
.slide::after {
  color: #888;
  background: none;
  height: 75px;
  right: 20px;
}
.slide::before {
  position: absolute;
  top: 75px;
  right: 100px;
  height: 50px;
}
.slide {
  padding-top: 60px;
  padding-left: 60px;
}
.slide.no-top {
  padding-top: 0;
}
.slide.center {
  text-align: center;
}
.slide h2 {
  color: #323a3e;
}
.hightlight,
.important,
.alert {
  border-radius: 0.2em;
  padding: 0.1em 0.3em;
}
.hightlight {
  background: #fafaa2;
}
.hightlight * {
  color: #000 !important;
}
.important {
  background: #a11212;
  color: #fff;
}
.important * {
  color: #fff !important;
}
.alert {
  background: #d10a0a;
  color: #fff;
}
.alert * {
  color: #fff !important;
}
.comment {
  color: #999;
}
.progress {
  border-color: #888 rgba(0,0,0,0) #888 #888;
}
.tag {
  color: #ad1531;
}
.attr {
  color: #4f7302;
}
.slide p.center {
  text-align: center;
}
.slide p.center img {
  max-width: 100%;
}
.slide p.quote {
  text-align: center;
  font-style: italic;
}
.slide.no-page::after {
  visibility: hidden;
}
.slide .copyright {
  position: absolute;
  right: 10px;
  bottom: 20px;
  transform: translateX(100%) rotate(-90deg);
  transform-origin: 0 100%;
  font-size: 12px;
  color: #888;
}
.slide .copyright.white {
  color: #fff;
}
blockquote {
  font-size: 35px;
  line-height: 1.4;
}
figcaption {
  opacity: 0.5;
  position: relative;
  line-height: 1.6;
  margin-top: 40px !important;
}
